import styled from 'styled-components';
import { GabaoGradientFull } from '~/assets';
import { getThemeByPath } from '~/utils';

const mediumSpacing = getThemeByPath('spacings.medium');
const mediumRadius = getThemeByPath('radius.medium');
const bigSpacing = getThemeByPath('spacings.big');
const smallSpacing = getThemeByPath('spacings.small');
const breakpointTablet = getThemeByPath('breakpoints.tablet');
const breakpointLaptop = getThemeByPath('breakpoints.laptop');
const breakpointLargeLaptop = getThemeByPath('breakpoints.largeLaptop');
const breakpointFhd = getThemeByPath('breakpoints.fhd');

const defaultAvatarSize = 100;
const defaultAvatarRadius = defaultAvatarSize / 2;
const defaultTextMarginTop = 96;

export const SectionWrapper = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 100vh;
`;

export const HeroWrapper = styled.div`
  display: flex;
  flex: 1;
  justify-content: flex-end;
  padding-right: 200px;

  background: url(${GabaoGradientFull}) no-repeat;
  background-size: contain;
  background-position-x: 0px;

  animation-name: hero-image-fade-in-to-0-x;
  animation-duration: 2000ms;
  animation-fill-mode: forwards;

  @media (max-width: ${breakpointFhd}px) {
    padding-right: 200px;
  }

  @media (max-width: ${breakpointLargeLaptop}px) {
    animation-name: hero-image-fade-in-to-100-x;
    background-position-x: -100px;
    padding-right: 100px;
  }

  @media (max-width: ${breakpointLaptop}px) {
    animation-name: hero-image-fade-in-to-200-x;
    background-position-x: -200px;
    padding-right: 50px;
  }

  @media (max-width: ${breakpointTablet}px) {
    animation-name: hero-image-fade-in-to-0-x;
    padding-right: ${bigSpacing}px;
    background-position-x: 0px;
    background-position-y: bottom;
    justify-content: flex-start;
    align-items: flex-end;
    flex-direction: column;
  }
`;

export const GradientMask = styled.div`
  background: rgb(0, 0, 0);
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 1) 100%
  );
`;

export const HeroTextWrapper = styled.div`
  display: flex;
  margin-top: ${defaultTextMarginTop}px;

  animation-name: hero-text-fade-in;
  animation-duration: 2000ms;
  animation-delay: 200ms;
`;

export const AvatarWrapper = styled.div`
  width: ${defaultAvatarSize}px;
  height: ${defaultAvatarSize}px;
  border-radius: ${defaultAvatarRadius}px;

  overflow: hidden;
`;

export const ContentWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  padding: ${bigSpacing}px;
`;

export const TitleWrapper = styled.div`
  display: flex;
  justify-content: center;
`;

export const DemosSubtitleWrapper = styled.div`
  display: flex;
  justify-content: center;
  margin-top: ${smallSpacing}px;
`;

export const CardsWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: ${mediumSpacing}px;

  @media (max-width: ${breakpointTablet}px) {
    flex-direction: column;
  }
`;

export const SectionCentralizer = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: center;
  justify-content: center;
`;

export const CardWrapper = styled.div`
  display: flex;
  margin: ${bigSpacing}px ${bigSpacing}px ${bigSpacing}px 0px;

  @media (max-width: ${breakpointTablet}px) {
    margin: ${bigSpacing}px 0px ${bigSpacing}px 0px;
  }
`;

export const SocialsWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: ${mediumSpacing}px;
`;

export const SocialWrapper = styled.div`
  display: flex;
  margin: ${smallSpacing}px ${smallSpacing}px ${smallSpacing}px
    ${smallSpacing}px;
`;

export const GoogleCalendarSubtitleWrapper = styled.div`
  margin-top: ${mediumSpacing}px;
`;

export const GoogleCalendarWrapper = styled.div`
  display: flex;
  justify-content: center;
  margin-top: ${mediumSpacing}px;
`;

export const IFrame = styled.iframe`
  border-radius: ${mediumRadius}px;
  background-color: #fff; // I know is not from the theme, but that MUST be this color...
  height: 840px;
  width: 100%;
  max-width: 1300px;
`;
