import React, { createContext, useState } from 'react';

type DashboardRoutingContextData = {
  isShowingDrawable: boolean;
  onChangeShowingDrawable: () => void;
  setIsShowingDrawable: (x: boolean) => void;
};

export const DashboardRoutingContext = createContext<DashboardRoutingContextData>(
  {} as DashboardRoutingContextData,
);

export const DashboardRoutingProvider: React.FC = ({ children }) => {
  const [isShowingDrawable, setIsShowingDrawable] = useState(false);

  const onChangeShowingDrawable = () => {
    setIsShowingDrawable(!isShowingDrawable);
  };

  return (
    <DashboardRoutingContext.Provider
      value={{
        isShowingDrawable,
        onChangeShowingDrawable,
        setIsShowingDrawable,
      }}
    >
      {children}
    </DashboardRoutingContext.Provider>
  );
};
