import styled from 'styled-components';
import { Typography } from '~/components/Atoms';
import { getThemeByPath } from '~/utils';

const defaultBtnWidth = 200;
const defaultBtnWidthMobile = 120;

const textMain = getThemeByPath('colors.text.main');

const radiusMedium = getThemeByPath('radius.medium');

const spacingShort = getThemeByPath('spacings.short');

const bkpTablet = getThemeByPath('breakpoints.tablet');

export const ButtonIconWrapper = styled.a`
  width: ${defaultBtnWidth}px;
  display: flex;
  height: fit-content;
  flex-direction: column;
  align-items: center;
  border-radius: ${radiusMedium}px;
  border: solid 2px ${textMain};
  padding: ${spacingShort}px;
  text-decoration: none;

  @media (max-width: ${bkpTablet}px) {
    width: ${defaultBtnWidthMobile}px;
  }

  @keyframes pulse-border {
    0% {
      border-color: ${textMain};
    }

    50% {
      border-color: #0000;
    }

    100% {
      border-color: ${textMain};
    }
  }

  &:hover {
    animation: pulse-border;
    animation-duration: 1500ms;
    animation-iteration-count: infinite;
  }
`;

export const ButtonTextWrapper = styled.div``;

export const ButtonIconText = styled(Typography).attrs({
  variant: 'header',
  textColor: 'colors.text.main',
  typographyProps: {
    style: {
      fontWeight: 500,
    },
  },
})``;

export const IconWrapper = styled.div`
  margin-top: ${spacingShort}px;
`;
