import React from 'react';
import { SVGIcons } from '~/assets';
import {
  ButtonIconText,
  ButtonIconWrapper,
  ButtonTextWrapper,
  IconWrapper,
} from './styles';

type Props = {
  btnText?: string;
  textColor?: string;
  icon?: SVGIconKeys;
  iconProps?: SVGIconProps;
  iconColor?: string;
  onClick?: () => void;
  href?: string;
  preventDefaultOnClick?: boolean;
};

const ButtonIcon: React.FC<Props> = ({
  btnText = 'Tradicionais',
  icon = 'google',
  iconProps,
  href = '',
  onClick,
  preventDefaultOnClick = false,
}) => {
  const handleClick: React.MouseEventHandler<HTMLAnchorElement> = e => {
    if (preventDefaultOnClick) e.preventDefault();

    if (onClick) onClick();
  };

  return (
    <ButtonIconWrapper onClick={handleClick} href={href}>
      <ButtonTextWrapper>
        <ButtonIconText>{btnText}</ButtonIconText>
      </ButtonTextWrapper>
      <IconWrapper>{SVGIcons[icon](iconProps)}</IconWrapper>
    </ButtonIconWrapper>
  );
};

export default ButtonIcon;
