import styled from 'styled-components';
import { Typography } from '~/components/Atoms';
import { getThemeByPath } from '~/utils';

type LineProps = {
  lineColor?: ColorThemeKeys;
  theme: AppTheme;
};

const lineColor = ({ theme, lineColor = 'colors.primary.main' }: LineProps) =>
  getThemeByPath(lineColor)({ theme });

const smallSpacing = getThemeByPath('spacings.small');

export const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const TextWrapper = styled.div`
  margin: 0 ${smallSpacing}px;
`;

export const Text = styled(Typography)``;

export const Line = styled.div<LineProps>`
  width: 100%;
  height: 1px;
  background-color: ${lineColor};
`;
