import React, { createContext, useState } from 'react';

type AuthContextData = {
  /**
   * The variable that wraps all logic that determines wether an user is authenticated.
   */
  isAuthenticated: () => boolean;
  credentials?: Credentials;
  updateCredentials: (x: Credentials) => void;
};

export const AuthContext = createContext<AuthContextData>(
  {} as AuthContextData,
);

export const AuthProvider: React.FC = ({ children }) => {
  const [credentials, setCredentials] = useState<Credentials | undefined>(
    undefined,
  );

  const updateCredentials = (updatedCredentials: Partial<Credentials>) => {
    setCredentials({ ...credentials, ...updatedCredentials });
  };

  const isAuthenticated = (): boolean => {
    if (credentials) {
      const { token, uid } = credentials;

      return !!token && !!uid;
    }

    return false;
  };

  return (
    <AuthContext.Provider
      value={{ isAuthenticated, updateCredentials, credentials }}
    >
      {children}
    </AuthContext.Provider>
  );
};
