import styled from 'styled-components';
import { getThemeByPath } from '~/utils';
import Typography from '../Typography';

type CellProps = {
  cellStatus: StepStatus;
  theme: AppTheme;
};

const defaultStepWidth = 120;
const defaultLineHeight = 4;

const primaryMain = getThemeByPath('colors.primary.main');
const textDisabled = getThemeByPath('colors.text.disabled');
const smallSpacing = getThemeByPath('spacings.small');
const mediumSpacing = getThemeByPath('spacings.medium');

const textColorByStatus: StepStatusGenericMapper<ColorThemeKeys> = {
  active: 'colors.primary.main',
  reached: 'colors.text.main',
  unreached: 'colors.text.disabled',
};

const lineColorByStatus: StepStatusGenericMapper<
  (props: CellProps) => string
> = {
  active: primaryMain,
  reached: primaryMain,
  unreached: textDisabled,
};

const handleTextColor = ({ cellStatus }: CellProps) => {
  return textColorByStatus[cellStatus];
};

const handleLineColor = ({ cellStatus }: CellProps) => {
  return lineColorByStatus[cellStatus];
};

export const Wrapper = styled.div`
  display: flex;
`;

export const StepWrapper = styled.div`
  min-width: ${defaultStepWidth}px;
  display: flex;
  flex-direction: column;
  margin-right: ${mediumSpacing}px;
`;

export const LabelWrapper = styled.div``;

export const LabelText = styled(Typography).attrs<CellProps>(props => ({
  textColor: handleTextColor(props),
}))<CellProps>``;

export const Line = styled.div<CellProps>`
  margin-top: ${smallSpacing}px;
  background-color: ${handleLineColor};
  height: ${defaultLineHeight}px;
  width: 100%;
`;
