import React, { FC } from 'react';
import {
  Footer,
  Header,
  Headline1,
  Headline2,
  Headline3,
  Headline4,
  Headline5,
  Headline6,
  Paragraph,
} from './styles';

type Props = {
  typographyProps?: React.HtmlHTMLAttributes<HTMLParagraphElement>;
  variant?: TypographyVariantKeys;
  textColor?: ColorThemeKeys;
  typographyAlign?: TypographyAlignKeys;
};

const Typography: FC<Props> = ({
  children,
  typographyProps,
  variant = 'paragraph',
  textColor = 'colors.text.main',
  typographyAlign,
}) => {
  const typographyComponents: TypographyVariantKeysGenericMapper<JSX.Element> = {
    paragraph: (
      <Paragraph
        textColor={textColor}
        textAlign={typographyAlign}
        {...typographyProps}
      >
        {children}
      </Paragraph>
    ),
    headline1: (
      <Headline1
        textColor={textColor}
        textAlign={typographyAlign}
        {...typographyProps}
      >
        {children}
      </Headline1>
    ),
    headline2: (
      <Headline2
        textColor={textColor}
        textAlign={typographyAlign}
        {...typographyProps}
      >
        {children}
      </Headline2>
    ),
    headline3: (
      <Headline3
        textColor={textColor}
        textAlign={typographyAlign}
        {...typographyProps}
      >
        {children}
      </Headline3>
    ),
    headline4: (
      <Headline4
        textColor={textColor}
        textAlign={typographyAlign}
        {...typographyProps}
      >
        {children}
      </Headline4>
    ),
    headline5: (
      <Headline5
        textColor={textColor}
        textAlign={typographyAlign}
        {...typographyProps}
      >
        {children}
      </Headline5>
    ),
    headline6: (
      <Headline6
        textColor={textColor}
        textAlign={typographyAlign}
        {...typographyProps}
      >
        {children}
      </Headline6>
    ),
    footer: (
      <Footer
        textColor={textColor}
        textAlign={typographyAlign}
        {...typographyProps}
      >
        {children}
      </Footer>
    ),
    header: (
      <Header
        textColor={textColor}
        textAlign={typographyAlign}
        {...typographyProps}
      >
        {children}
      </Header>
    ),
  };

  return typographyComponents[variant];
};

export default Typography;
