import { createGlobalStyle } from 'styled-components';
import breakpoints from './breakpoints';
import spacings from './spacings';

const { tablet, laptop, largeLaptop, fhd } = breakpoints;
const { insane: insaneSpacing } = spacings;

export default createGlobalStyle`
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 1.4rem;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    
    @media (max-width: ${fhd}px) {
      font-size: 1.2rem;
    }

    @media (max-width: ${largeLaptop}px) {
      font-size: 1.0rem;
    }

    @media (max-width: ${laptop}px) {
      font-size: 0.8rem;
    }

    @media (max-width: ${tablet}px) {
      font-size: 0.6rem;
    }
  }

  body {
    display: flex;
    flex-direction: column;
    flex: 1;
  }

  button,
  input,
  select {
    border: none;
    outline: none;
    background-color: #0000;
  }

  // Animations
  @keyframes opacity-fade-in {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @keyframes opacity-fade-out {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

  @keyframes padding-left-fade-in {
    0% {
      padding-left: ${insaneSpacing}px;
    }
    100% {
      padding-left: 0px;
    }
  }

  @keyframes padding-left-fade-out {
    0% {
      padding-left: 0px;
    }
    100% {
      padding-left: ${insaneSpacing}px;
    }
  }

  @keyframes padding-right-fade-in {
    0% {
      padding-right: ${insaneSpacing}px;
    }
    100% {
      padding-right: 0px;
    }
  }

  @keyframes padding-right-fade-out {
    0% {
      padding-right: 0px;
    }
    100% {
      padding-right: ${insaneSpacing}px;
    }
  }

  @keyframes hero-text-fade-in {
    0% {
      opacity: 0;
      margin-right: ${insaneSpacing}px;
    }
    100% {
      opacity: 1;
      margin-right: 0px;
    }
  }

  @keyframes hero-image-fade-in-to-0-x {
    0% {
      background-position-x: -30px;
    }

    100% {
      background-position-x: 0px;
    }
  }

  @keyframes hero-image-fade-in-to-100-x {
    0% {
      background-position-x: -150px;
    }

    100% {
      background-position-x: -100px;
    }
  }

  @keyframes hero-image-fade-in-to-200-x {
    0% {
      background-position-x: -250px;
    }

    100% { 
      background-position-x: -200px;
    }
  }
`;
