import styled from 'styled-components';
import { Typography } from '~/components/Atoms';
import { getThemeByPath } from '~/utils';

type WrapperProps = {
  theme: AppTheme;
  headerColor?: ColorThemeKeys;
};

const defaultHeaderHeight = 80;

const mediumSpacing = getThemeByPath('spacings.medium');
const breakpointTablet = getThemeByPath('breakpoints.tablet');

const handleBackgroundColorWrapper = ({
  theme,
  headerColor = 'colors.environment.darker',
}: WrapperProps) => getThemeByPath(headerColor)({ theme });

export const Wrapper = styled.div<WrapperProps>`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: ${defaultHeaderHeight}px;
  padding: ${mediumSpacing}px;
  background-color: ${handleBackgroundColorWrapper};
`;

export const PressableWrapper = styled.div`
  display: flex;
`;

export const Pressable = styled.button`
  display: none;

  @media (max-width: ${breakpointTablet}px) {
    display: flex;
  }

  &:hover {
    cursor: pointer;
  }
`;

export const IconWrapper = styled.div``;

export const CurrentSceneText = styled(Typography).attrs({
  variant: 'headline3',
})``;
